@Component({
  components: {
    DrawNumbers: () => import('@/components/GameIframe/GameContent/DrawNumbers.vue'),
    DragonLuzhu: () => import('@/components/GameIframe/GameContent/DragonLuzhu.vue'),
  },
})

export default class QuickViewDrawHistory extends Vue {
