







































import { Component, Vue, Prop, Emit } from 'vue-property-decorator';

@Component({})

export default class BetOfficialPlay extends Vue {
  private activeOption?: string = '';
  private selectedOption: any[] = [];
  private selectedNumbers: any[] = [];
  @Prop({type: String, default: '' }) public readonly optionTitle!: string;
  @Prop({type: String, default: 'PK10' }) public readonly gameType!: string;
  @Prop({type: Array, default: () => { [] } }) public readonly betNumbers!: any[];
  @Prop({type: Array, default: () => { [] } }) public readonly betOptions!: any[];
  private selectOptions(option: string) {
    //  全 = 0,  大  = 1,  小  = 2,  单 = 3,  双  = 4,  清  = 5
    if (option != '清') {
      if (this.activeOption == option) {
        this.activeOption = '';
        this.selectedNumbers = [];
      } else {
        this.activeOption = option;
        this.selectedNumbers = [];
        if (option == '全') {
          this.selectedNumbers = [...this.betNumbers];
        } else if (option == '大') {
          this.selectedNumbers = [...this.selectBigNums(this.betNumbers)];
        } else if (option == '小') {
          this.selectedNumbers = [...this.selectSmallNums(this.betNumbers)];
        } else if (option == '单') {
          this.selectedNumbers = [...this.selectSingleNums(this.betNumbers)];
        } else {
          this.selectedNumbers = [...this.selectDoubleNums(this.betNumbers)];
        } 
      }
    } else {
      this.activeOption = '';
      this.selectedNumbers = [];
    }
  }
  private selectNumber(itemVal: any) {
    let temp:any = [];
    temp.push(itemVal);
    if (this.selectedNumbers.length == 0) {
      this.selectedNumbers = [...temp]
    } else {
      let isExist = this.selectedNumbers.findIndex((val: any) => val == itemVal);
      if (isExist == -1) {
        this.selectedNumbers = [...this.selectedNumbers, ...temp]
      } else {
        if (this.selectedNumbers.length == 0) {
          this.selectedNumbers = [];
        } else {
          this.selectedNumbers.splice(isExist, 1);
        }
      }
    }

    if (this.selectedNumbers.length == this.betNumbers.length) {
      this.activeOption = '全';
    }
    if (this.selectedNumbers.length < this.betNumbers.length) {
      this.activeOption = '';
    }
  }
  private setItemClass(itemIndex: number) {
    let isExist = this.selectedNumbers.findIndex((val: any) => val == itemIndex);
    if (isExist == -1) return null;
    return 'number-active';
  }
  private selectBigNums(arr: any) {
    return arr.filter((val: any, index: any) => {
      return val > 4;
    });
  }
  private selectSmallNums(arr: any) {
    return arr.filter((val: any, index: any) => {
      return val < 5;
    });
  }
  private selectSingleNums(arr: any) {
    return arr.filter((val: any, index: any) => {
      return val % 2 == 1;
    });
  }
  private selectDoubleNums(arr: any) {
    return arr.filter((val: any, index: any) => {
      return val % 2 == 0;
    });
  }
}
